body {
  font-family: 'Titillium Web', sans-serif;
  text-align: center;
}
.container {
  position: relative;
  margin-top: 25px;
  lost-center: 980px;
}
.col {
  position: relative;
  height: 50px;
  line-height: 50px;
  text-align: center;
  background: #bad455;
}
.row--bg {
  position: absolute;
  width: 100%;
  top: -15px;
  left: 0;
}
.row--bg .col {
  height: 480px;
  background: #f1f6dd;
}
.row:nth-of-type(6) .col .col {
  background: #a1be30;
}
